import { FC, useMemo, MouseEvent } from 'react';
import styled, { css } from 'styled-components';

import {
  Card,
  CardHeader,
  CardContent,
  CardFooter,
  Button,
  useI18n,
  ForwardProps,
  NoChildrenProp,
  defaultThemeProp,
  ListToolbar,
  ListToolbarProps,
  SearchInputProps,
  EmptyState,
  useTestIds,
  TestIdProp,
  AvatarProps,
  Avatar,
  withTestIds
} from '@pega/cosmos-react-core';
import { ViewSelectorProps } from '@pega/cosmos-react-core/lib/components/ListToolbar/ListToolbar.types';

import TaskList, { TaskItem, TaskItemProps } from './TaskList';
import { getTasksTestIds } from './Tasks.test-ids';

export interface TasksProps extends NoChildrenProp, TestIdProp {
  /** An array of TaskItemProps to generate the list. */
  items: TaskItemProps[];
  /** Heading text to render at the top of the list. */
  name: ListToolbarProps['name'];
  /** A visual to accompany the header text. */
  avatar?: Pick<AvatarProps, 'name' | 'icon' | 'imageSrc' | 'status'>;
  /** Definition of view selection. */
  viewSelector?: Pick<ViewSelectorProps, 'views' | 'onViewSelect'>;
  /** Integer representing the total count of tasks. */
  count?: number;
  /** Opt-in search configuration. */
  search?: Pick<SearchInputProps, 'value' | 'onSearchChange'>;
  /** Callback for when to show more/less button is clicked. Lists greater than 3 items will display the show toggle button in its footer if passed. */
  onExpandToggle?: () => void;
}

export const StyledTasks = styled(Card)<{ openItem?: boolean }>(({ theme, openItem }) => {
  return openItem
    ? css`
        box-shadow: ${theme.base.shadow.low};
      `
    : null;
});

StyledTasks.defaultProps = defaultThemeProp;

const Tasks: FC<ForwardProps & TasksProps> = (props: TasksProps) => {
  const {
    testId,
    items = [],
    name,
    avatar,
    viewSelector,
    count = items.length,
    search,
    onExpandToggle,
    ...restProps
  } = props;
  const testIds = useTestIds(testId, getTasksTestIds);
  const openItem = items.find(item => item.content);
  const t = useI18n();

  const hasFooter = !!(!openItem && onExpandToggle && count > 0);

  const TaskItems = useMemo(() => {
    return openItem ? (
      <TaskItem {...openItem} />
    ) : (
      <TaskList items={items} hasFooter={hasFooter} testId={testIds.root} />
    );
  }, [items, openItem]);

  return (
    <StyledTasks data-testid={testIds.root} {...restProps} openItem={!!openItem} data-app-region>
      {!openItem && (
        <CardHeader container={{ justify: 'start', alignItems: 'center', gap: 1 }}>
          {avatar && <Avatar {...avatar} />}
          <ListToolbar
            name={name}
            headingTag='h2'
            viewSelector={viewSelector}
            count={{ total: count }}
            search={search}
            item={{ grow: 1 }}
          />
        </CardHeader>
      )}
      <CardContent>{items.length > 0 ? TaskItems : <EmptyState />}</CardContent>

      {hasFooter && (
        <CardFooter justify='center'>
          <Button
            data-testid={testIds.showMoreLess}
            variant='link'
            aria-label={t(!!count && count > items.length ? 'show_more' : 'show_less', ['tasks'])}
            onClick={(e: MouseEvent<HTMLButtonElement>) => {
              onExpandToggle?.();
              if (!!count && count <= items.length) {
                const { currentTarget } = e;
                requestAnimationFrame(() => {
                  if (currentTarget.getBoundingClientRect().top < 0) currentTarget.scrollIntoView();
                });
              }
            }}
          >
            {t(!!count && count > items.length ? 'show_more' : 'show_less')}
          </Button>
        </CardFooter>
      )}
    </StyledTasks>
  );
};

export default withTestIds(Tasks, getTasksTestIds);
