import {
  forwardRef,
  FunctionComponent,
  PropsWithoutRef,
  ReactNode,
  Ref,
  MouseEvent,
  useEffect,
  useRef
} from 'react';
import styled, { css } from 'styled-components';

import {
  ForwardProps,
  SummaryItem,
  StyledSummaryListItem,
  Flex,
  defaultThemeProp,
  Button,
  useI18n,
  VisuallyHiddenText,
  Breadcrumbs,
  StyledBreadcrumbs,
  StyledText,
  useTestIds,
  TestIdProp,
  useLiveLog,
  debounce,
  Actions,
  ActionsProps,
  withTestIds
} from '@pega/cosmos-react-core';

import { getTaskListTestIds, getTaskItemTestIds } from './Tasks.test-ids';

export interface TaskItemProps extends TestIdProp {
  id: string;
  /** Name of the task displayed in the list. */
  name: string;
  /** Name of the process for the task. */
  processName?: string;
  /** Avatar element to represent the task's assignee. */
  avatar?: ReactNode;
  /** MetaList element with info related to the task. */
  meta?: ReactNode;
  /** Provide a callback to render a button for a task. */
  onOpen?: (id: TaskItemProps['id'], e: MouseEvent<HTMLButtonElement>) => void;
  /** Content for the task when open. */
  content?: ReactNode;
  /** An array of actions to add to open task. */
  additionalActions?: ActionsProps['items'];
}

export interface TaskListProps extends TestIdProp {
  items: TaskItemProps[];
  hasFooter?: boolean;
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLElement>;
}

interface Announcement {
  name: string;
  status: 'removed' | 'added';
}

const StyledTaskList = styled.ul<{ hasFooter: boolean }>(({ theme, hasFooter }) => {
  return css`
    ${hasFooter &&
    css`
      > li:last-child {
        border-bottom: 0.0625rem solid ${theme.base.palette['border-line']};
      }
    `}

    ${StyledBreadcrumbs} > ${StyledText} {
      font-weight: ${theme.base['font-weight']['semi-bold']};
    }
  `;
});

StyledTaskList.defaultProps = defaultThemeProp;

export const TaskItem = withTestIds(
  ({
    testId,
    id,
    name,
    avatar,
    meta,
    processName,
    onOpen,
    content,
    addAnnouncement,
    additionalActions,
    ...restProps
  }: TaskItemProps & { addAnnouncement?: (announcement: Announcement) => void }) => {
    const testIds = useTestIds(testId, getTaskItemTestIds);
    const t = useI18n();

    useEffect(() => {
      addAnnouncement?.({ name, status: 'added' });

      return () => {
        addAnnouncement?.({ name, status: 'removed' });
      };
    }, []);

    const summary = (
      <SummaryItem
        visual={avatar}
        primary={
          processName ? (
            <Breadcrumbs
              path={[
                { id: 'processName', primary: processName },
                { id: 'name', primary: name }
              ]}
            />
          ) : (
            name
          )
        }
        secondary={meta}
        actions={
          content
            ? additionalActions && <Actions menuAt={1} items={additionalActions} />
            : onOpen && (
                <Button
                  data-testid={testIds.openTask}
                  variant='primary'
                  onClick={(e: MouseEvent<HTMLButtonElement>) => onOpen(id, e)}
                >
                  <span>{t('go')}</span>
                  <VisuallyHiddenText>{`-${processName ?? ''} ${name}`}</VisuallyHiddenText>
                </Button>
              )
        }
      />
    );

    return (
      <Flex
        data-testid={testIds.root}
        {...restProps}
        container={{ direction: 'column', gap: 1 }}
        item={{ grow: 1 }}
        as={StyledSummaryListItem}
        forwardedAs='li'
      >
        {summary}
        {content}
      </Flex>
    );
  },
  getTaskItemTestIds
);

const TaskList: FunctionComponent<TaskListProps & ForwardProps> = forwardRef(function TaskList(
  { testId, items, hasFooter = false }: PropsWithoutRef<TaskListProps>,
  ref
) {
  const testIds = useTestIds(testId, getTaskListTestIds);
  const t = useI18n();
  const { announcePolite } = useLiveLog();
  const announcements = useRef<Announcement[]>([]);

  const debouncedAnnounce = debounce(() => {
    if (announcements.current.length > 0) {
      let tasksAdded = 0;
      let tasksRemoved = 0;
      announcements.current.forEach(item => {
        if (item.status === 'removed') {
          tasksRemoved += 1;
        } else {
          tasksAdded += 1;
        }
      });
      let message = tasksAdded ? t('tasks_added', [tasksAdded], { count: tasksAdded }) : '';
      if (tasksRemoved) {
        message += t('tasks_removed', [tasksRemoved], { count: tasksRemoved });
      }

      announcePolite({ message });
      announcements.current = [];
    }
  }, 2000);

  const addAnnouncement = (announcement: Announcement) => {
    announcements.current = [...announcements.current, announcement];
    debouncedAnnounce();
  };

  return (
    <Flex
      data-testid={testIds.root}
      container={{ direction: 'column' }}
      as={StyledTaskList}
      hasFooter={hasFooter}
      ref={ref}
    >
      {items.map((item, i) => (
        <TaskItem {...item} addAnnouncement={addAnnouncement} key={`${i + 1}`} />
      ))}
    </Flex>
  );
});

export default withTestIds(TaskList, getTaskListTestIds);
