import {
  forwardRef,
  FunctionComponent,
  KeyboardEvent,
  PropsWithoutRef,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
  MouseEvent
} from 'react';

import Actions from '../Actions';
import Button from '../Button';
import { Action, ForwardProps } from '../../types';
import {
  useBreakpoint,
  useConfiguration,
  useConsolidatedRef,
  useEscape,
  useI18n,
  useTestIds
} from '../../hooks';
import Flex from '../Flex';
import SearchInput from '../SearchInput';
import Icon, { registerIcon } from '../Icon';
import * as filterIcon from '../Icon/icons/filter.icon';
import * as arrowUpDownIcon from '../Icon/icons/arrow-up-down.icon';
import * as rowIcon from '../Icon/icons/row.icon';
import * as plusIcon from '../Icon/icons/plus.icon';
import * as moreIcon from '../Icon/icons/more.icon';
import { hasProp, withTestIds } from '../../utils';
import { formatNumber } from '../Number';
import MenuButton from '../MenuButton';
import Avatar from '../Avatar';

import PresetMenuPopover, { PresetMenuPopoverProps } from './PresetMenuPopover';
import {
  StyledViewSelector,
  StyledSearchForm,
  StyledListToolbar,
  StyledHeadingText,
  StyledCountMeta,
  StyledContainer,
  StyledListToolbarContent,
  StyledListToolbarDialog
} from './ListToolbar.styles';
import type {
  ListToolbarProps,
  PresetMenuProps,
  QueryOptionDialogProps,
  QueryOptionId,
  ViewProps,
  ViewSelectorProps
} from './ListToolbar.types';
import helpers from './helpers';
import { getListToolbarTestIds } from './ListToolbar.test-ids';

registerIcon(filterIcon, arrowUpDownIcon, rowIcon, plusIcon, moreIcon);

const CountMeta = ({
  count,
  ...restProps
}: { count: NonNullable<ListToolbarProps['count']> } & ForwardProps) => {
  const { locale } = useConfiguration();
  const t = useI18n();
  const totalFormattedValue = formatNumber(count.total ?? 0, {
    locale,
    options: {
      useGrouping: true
    }
  });
  return (
    <StyledCountMeta variant='secondary' {...restProps}>
      {count.selected
        ? t(
            'selected_count',
            [formatNumber(count.selected, { locale, options: { useGrouping: true } })],
            { count: count.selected }
          )
        : count.total !== undefined &&
          t(
            'results_count',
            [count.totalHasMore ? `${totalFormattedValue}+` : totalFormattedValue],
            {
              count: count.total
            }
          )}
    </StyledCountMeta>
  );
};

const generateViewName = (
  view: ViewProps,
  texts: { default: string; appDefault: string },
  options: {
    defaultId?: ViewSelectorProps['defaultId'];
    appDefaultId?: ViewSelectorProps['appDefaultId'];
  }
) => {
  return `${view.text} ${view.id === options.defaultId ? `(${texts.default})` : ''} ${
    view.id === options.appDefaultId ? `(${texts.appDefault})` : ''
  }`;
};

const ListToolbar: FunctionComponent<ListToolbarProps & ForwardProps> = forwardRef(
  function ListToolbar(
    {
      testId,
      name,
      headingTag = 'h3',
      viewSelector,
      formControlProps,
      createNew,
      search,
      count,
      additionalActions,
      filter,
      sort,
      group,
      actions,
      actionsButtonRef,
      ...restProps
    }: PropsWithoutRef<ListToolbarProps>,
    ref: ListToolbarProps['ref']
  ) {
    const t = useI18n();
    const testIds = useTestIds(testId, getListToolbarTestIds);

    const isSmallOrAbove = useBreakpoint('sm');
    const consolidatedActionsButtonRef = useConsolidatedRef(actionsButtonRef);
    const toolbarContentRef = useRef<HTMLDivElement>(null);
    const searchInputRef = useRef<HTMLInputElement>(null);
    const searchWrapperRef = useRef<HTMLDivElement>(null);
    const searchButtonRef = useRef<HTMLButtonElement>(null);
    const [isSearchOpen, setIsSearchOpen] = useState(!!search?.value);

    const onSearchEscape = useCallback(() => {
      if (!search?.value) {
        setIsSearchOpen(false);
      }
      setTimeout(() => searchButtonRef.current?.focus());
    }, [search?.value]);

    useEffect(() => {
      if (search?.value) {
        setIsSearchOpen(true);
      }
    }, [search?.value]);

    const [currentQueryOptionId, setCurrentQueryOptionId] = useState<QueryOptionId | null>(null);
    const queryOptionPopoverTarget = useRef<HTMLButtonElement>();

    const [actionsQueryOptionPreset, setActionsQueryOptionPreset] =
      useState<Pick<PresetMenuPopoverProps, 'heading' | 'menu'>>();
    const [optionPresetMenuOpen, setOptionPresetMenuOpen] = useState(false);

    const createNewButton = useMemo(() => {
      if (!createNew?.onClick) return null;
      const text = createNew.label ?? t('create_new');

      return (
        <Button variant='simple' icon label={text} aria-label={text} onClick={createNew.onClick}>
          <Icon name='plus' />
        </Button>
      );
    }, [t, createNew, isSmallOrAbove]);

    useEscape(onSearchEscape, searchButtonRef);
    const searchInputWrapper = useMemo(
      () => (
        <div role='dialog' ref={searchWrapperRef}>
          <SearchInput
            {...search}
            onSearchSubmit={(value: string) => {
              search?.onSearchSubmit?.(value);
              onSearchEscape();
            }}
            onClick={(e: MouseEvent<HTMLInputElement>) => {
              e.preventDefault();
            }}
            ref={searchInputRef}
          />
        </div>
      ),
      [search, onSearchEscape]
    );

    const searchLabel = t('search');
    const searchButton = useMemo(() => {
      return (
        <Button
          as='div'
          role='button'
          variant='simple'
          icon
          label={!isSearchOpen ? searchLabel : undefined}
          aria-label={searchLabel}
          onClick={() => {
            setIsSearchOpen(true);
            setTimeout(() => searchInputRef.current?.focus());
          }}
          onKeyDown={(e: KeyboardEvent<HTMLElement>) => {
            if (e.target !== searchInputRef.current && (e.key === 'Enter' || e.key === ' ')) {
              setIsSearchOpen(true);
              setTimeout(() => {
                searchInputRef.current?.focus();
                searchInputRef.current?.select();
              });
            }
          }}
          tabIndex={0}
          ref={searchButtonRef}
        >
          {isSearchOpen ? searchInputWrapper : <Icon name='search' />}
        </Button>
      );
    }, [searchLabel, setIsSearchOpen, isSearchOpen, searchInputWrapper]);

    let queryOptionPopover = null;
    if (currentQueryOptionId) {
      const queryOptionProps = { filter, sort, group }[currentQueryOptionId];
      if (
        queryOptionProps &&
        hasProp(queryOptionProps, 'renderer') &&
        queryOptionPopoverTarget.current
      ) {
        queryOptionPopover = (
          <StyledListToolbarDialog
            target={queryOptionPopoverTarget.current}
            heading={t(currentQueryOptionId)}
            placement='bottom-end'
            onCancel={() => {
              if (queryOptionProps.onCancel() === false) return;
              setCurrentQueryOptionId(null);
            }}
            onSubmit={() => {
              if (queryOptionProps.onSubmit() === false) return;
              setCurrentQueryOptionId(null);
            }}
          >
            <queryOptionProps.renderer />
          </StyledListToolbarDialog>
        );
      }
    }

    const getQueryOptionAction = useCallback(
      (
        id: 'sort' | 'group' | 'filter',
        icon: string,
        queryOptionProps: QueryOptionDialogProps | PresetMenuProps
      ) => {
        return {
          text: t(id),
          id,
          icon,
          count: queryOptionProps.count,
          onClick: () => {
            if (hasProp(queryOptionProps, 'renderer')) {
              setCurrentQueryOptionId(id);
              queryOptionPopoverTarget.current = consolidatedActionsButtonRef.current ?? undefined;
            }
            if (hasProp(queryOptionProps, 'items')) {
              setActionsQueryOptionPreset({
                heading: t(id),
                menu: {
                  mode: queryOptionProps.mode,
                  items: queryOptionProps.items.map(item => ({ ...item, primary: item.text })),
                  onItemClick: queryOptionProps.onItemClick
                }
              });
              setOptionPresetMenuOpen(true);
            }
          }
        };
      },
      []
    );

    const basicModeActions = useMemo(() => {
      let basicActions: Action[] = [];
      if (filter) {
        const filterAction: Action = getQueryOptionAction('filter', 'filter', filter);
        basicActions.push(filterAction);
      }
      if (sort) {
        const sortAction: Action = getQueryOptionAction('sort', 'arrow-up-down', sort);
        basicActions.push(sortAction);
      }
      if (group) {
        const groupAction: Action = getQueryOptionAction('group', 'row', group);
        basicActions.push(groupAction);
      }
      if (actions) {
        basicActions = basicActions.concat(actions);
      }

      return basicActions.length ? basicActions : undefined;
    }, [actions, filter, sort, group, getQueryOptionAction]);

    let selectedViewName: string | undefined;
    if (viewSelector) {
      selectedViewName = helpers.getSelectedView(viewSelector.views)?.text;
      selectedViewName ??= helpers.getViews(viewSelector.views)[0].text;
    }

    const labelText =
      viewSelector && viewSelector.views.length > 1 ? `${name}: ${selectedViewName}` : name;

    return (
      <Flex data-testid={testIds.root} container {...restProps} as={StyledListToolbar} ref={ref}>
        <StyledListToolbarContent
          container={{ direction: 'column', gap: 0.5 }}
          ref={toolbarContentRef}
          item={{ grow: 1 }}
        >
          <Flex container={{ alignItems: 'start', justify: 'between', gap: 2 }}>
            <StyledContainer
              container={{
                alignItems: 'baseline',
                gap: 1,
                wrap: 'wrap'
              }}
            >
              <StyledHeadingText
                data-testid={testIds.heading}
                variant={headingTag}
                required={viewSelector?.views?.length && formControlProps?.required}
              >
                {!viewSelector || viewSelector.views.length <= 1 ? (
                  labelText
                ) : (
                  <MenuButton
                    variant='text'
                    text={labelText}
                    as={StyledViewSelector}
                    menu={{
                      mode: 'single-select',
                      items: viewSelector.views.map(view => {
                        if (hasProp(view, 'items')) {
                          return {
                            id: view.text,
                            label: view.text,
                            items: view.items.map(groupedView => {
                              return {
                                ...groupedView,
                                primary: generateViewName(
                                  groupedView,
                                  { default: t('default'), appDefault: t('app_default') },
                                  {
                                    defaultId: viewSelector.defaultId,
                                    appDefaultId: viewSelector.appDefaultId
                                  }
                                ),
                                visual: groupedView.visual ? (
                                  <Avatar {...groupedView.visual} />
                                ) : undefined
                              };
                            })
                          };
                        }

                        return {
                          ...view,
                          primary: generateViewName(
                            view,
                            { default: t('default'), appDefault: t('app_default') },
                            {
                              defaultId: viewSelector.defaultId,
                              appDefaultId: viewSelector.appDefaultId
                            }
                          ),
                          visual: view.visual ? <Avatar {...view.visual} /> : undefined
                        };
                      }),
                      onItemClick: viewSelector.onViewSelect
                    }}
                    testId={testIds.viewSelector}
                  />
                )}
              </StyledHeadingText>
              {isSmallOrAbove && count && <CountMeta data-testid={testIds.count} count={count} />}
            </StyledContainer>
            <Flex container={{ alignItems: 'center', gap: 1 }} item={{ shrink: 0 }}>
              {isSmallOrAbove && additionalActions}
              {search && isSmallOrAbove && searchButton}
              {isSmallOrAbove && createNewButton}
              {isSmallOrAbove && basicModeActions && (
                <span>
                  <Actions
                    items={basicModeActions.map(({ icon, ...restActionProps }) => ({
                      visual: icon ? <Icon name={icon} /> : undefined,
                      ...restActionProps
                    }))}
                    menuAt={1}
                    ref={consolidatedActionsButtonRef}
                  />
                </span>
              )}
              {!isSmallOrAbove && count && <CountMeta data-testid={testIds.count} count={count} />}
            </Flex>
          </Flex>
          {!isSmallOrAbove && (
            <Flex container={{ alignItems: 'start', justify: 'between', gap: 1 }}>
              {additionalActions}
              <Flex
                as={StyledSearchForm}
                container={{ alignItems: 'center', wrap: 'wrap', gap: 1 }}
                item={{ grow: 1 }}
              >
                {search && searchInputWrapper}
              </Flex>
              {createNewButton}
              {basicModeActions && (
                <span>
                  <Actions
                    items={basicModeActions.map(({ icon, ...restActionProps }) => ({
                      visual: icon ? <Icon name={icon} /> : undefined,
                      ...restActionProps
                    }))}
                    menuAt={1}
                    ref={consolidatedActionsButtonRef}
                  />
                </span>
              )}
            </Flex>
          )}
        </StyledListToolbarContent>

        {queryOptionPopover}

        <PresetMenuPopover
          buttonRef={consolidatedActionsButtonRef}
          heading={actionsQueryOptionPreset?.heading}
          menu={actionsQueryOptionPreset?.menu}
          isOpen={optionPresetMenuOpen}
          onClose={() => setOptionPresetMenuOpen(false)}
        />
      </Flex>
    );
  }
);

export default withTestIds(ListToolbar, getListToolbarTestIds);
